.container {
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;

  @media screen and (min-width: $tablet) {
    display: flex;
    padding-left: 67px;
    padding-right: 67px;
  }
}

.section {
  background-color: $bgd-color;
}

.text__content {
  @media screen and (min-width: $tablet) {
    margin-right: 20px;
  }
}

.section__title {
  font-family: $style_font;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 2.71;

  text-align: center;

  color: $first_color;
}

.section__text {
  font-family: $first_font;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;

  color: $first_color;
}

.section__text--indent {
  display: block;
  margin-bottom: 10px;
}

.text__items {
  margin-bottom: 10px;
}

.text__item::before {
  content: ">";
  font-family: $first_font;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;

  color: $first_color;
}

.text__item:not(:last-child) {
  margin-bottom: 10px;
}

.button {
  margin-bottom: 30px;

  font-family: $style_font;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 3.61;

  border: none;
  background-color: transparent;
  color: $button_color;
}

.feature__set {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: $tablet) {
    padding-top: 50px;
  }
}

.feature__miniset {
  display: flex;
  margin-left: auto;
  margin-right: auto;

  height: 173px;
}

.feature__miniset:not(:last-child) {
  margin-bottom: 30px;

  @media screen and (min-width: $tablet) {
    margin-bottom: 55px;
  }
}

.feature {
  flex-basis: calc((100% - 2 * 5px) / 2);
  margin-right: 5px;

  @media screen and (min-width: $tablet) {
    margin-right: 15px;
  }
}
.feature--end {
  align-self: flex-end;
}

.feature__thumb {
  position: relative;
  display: block;
  width: 142px;
  height: 142px;

  background-image: url("/images/circle.svg");
  background-position-x: 50%;
  background-position-y: 100%;

  background-repeat: no-repeat;
}

.feature__image {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: transparent;
}

.feature__title {
  position: absolute;
  width: 100%;
  top: 50px;
  left: 50%;
  transform: translate(-50%, 0);

  font-family: $style_font;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.28;
  text-align: center;

  color: #000000;
}

.feature__text {
  position: absolute;
  width: 100%;
  bottom: 40px;
  left: 50%;
  transform: translate(-50%, 0);

  font-family: $first_font;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
  text-align: center;

  color: #000000;
}

.feature__icon {
  position: absolute;
  display: block;
  bottom: 14px;
  left: 50%;
  transform: translate(-50%, 0);

  width: 19px;
  height: 18px;
}
